function WineFilter() {
  const navigate = useNavigate();
  const [optionModal, setOptionModal] = useState({
    visible: false,
  });
  return (
    <div>
      <Row>
        {filters.map((item) => {
          return (
            <Col span={8}>
              <Card
                bordered={false}
                cover={<img alt="example" src={eventImg} />}
                style={{ margin: "10px", cursor: "pointer" }}
                onClick={() => {
                  setOptionModal({
                    ...optionModal,
                    visible: true,
                  });
                }}
              >
                {item.title}
              </Card>
            </Col>
          );
        })}
      </Row>
      <Picker
        columns={flavors}
        visible={optionModal.visible}
        confirmText=""
        onCancel={() => {
          setOptionModal({
            ...optionModal,
            visible: false,
          });
        }}
        onConfirm={(v) => {
          navigate("/products");
        }}
      />
    </div>
  );
}

export default WineFilter;
